import {documentReady} from "../../js/helpers/documentReady";
import {App} from "../../js/App";
import {Dropdown} from "../dropdown";
import {FormRange} from "./form-calc__range";

function initDropdown() {
	try {
		const nodes = document.querySelectorAll('.js-dropdown');
		if (!nodes || !nodes.length) return false;
		nodes.forEach((node) => new Dropdown(node));
	} catch (error) {
		App.debug(error)
	}
}

function initRange() {
	try {
		const nodes = document.querySelectorAll('.js-form-calc-range');
		if (!nodes || !nodes.length) return false;
		return nodes.forEach(node => new FormRange(node));
	} catch (error) {
		App.debug(error);
	}
}

documentReady(() => {
	initDropdown();
	initRange();
})


