const instances = [];

export class Dropdown {
	constructor(element) {
		this.el = element
		this._head = this.el.querySelector('.dropdown__head');
		this._value = this.el.querySelector('.dropdown__value');
		this.select = element.querySelector('select');
		this.options = element.querySelectorAll('option,.dropdown__option');
		this.value = this.select.value;
		this.disabled = this.select.disabled;

		instances.push(this);

		this.el.classList.toggle('is-disabled', this.disabled);
		this.select.addEventListener('change', () => {
			this.value = this.select.value;
		});
		this.el.addEventListener('click', (event) => {
			event.stopPropagation();
			if (!this.disabled) {
				instances.forEach((instance) => {
					if (instance !== this) {
						instance.close();
					}
				});
				const option = event.target.closest('.dropdown__option');
				if (option) {
					this.value = option.dataset.value;
					this.close();
				}
			}

		});
		this._head.addEventListener('click', () => {
			if (!this.disabled) {
				this.el.classList.toggle('is-active');
			}
		});
		document.addEventListener('click', () => this.close());
	}
	close() {
		this.el.classList.remove('is-active');
	}

	set value(value) {
		this._value.innerText = value;
		this.el.classList.toggle('has-value', value);
		this.options.forEach((option) => {
			const optionValue = option.value || option.dataset.value;
			const isSelected = optionValue === value
			option.classList.toggle('is-selected', isSelected);
			if (option.tagName === 'OPTION') {
				if (isSelected) {
					option.setAttribute('selected', '');
				} else {
					option.removeAttribute('selected');
				}
			}
		});
	}
}
