import { Range } from '../../range';

export class FormRange {
  constructor(node) {
    this.el = node;
    this._slider = this.el.querySelector('.form-calc__range-slider');
    this._from = this.el.querySelector('.form-calc__range-from');
    this._to = this.el.querySelector('.form-calc__range-to');
    this.slider = new Range(this._slider);
    this._values = [this._from.value, this._to.value];
    this.slider.values = this._values;
    this.slider.onslide = (values) => {
      this.values = values;
      this._from.dispatchEvent(new Event('change', {
        bubbles: true,
      }));
      this._to.dispatchEvent(new Event('change', {
        bubbles: true,
      }));
    };
    this._from.addEventListener('change', () => this.onChange());
    this._to.addEventListener('change', () => this.onChange());
    this._from.addEventListener('keyup', () => this.onInput());
    this._from.addEventListener('focus', () => this.onFocus());
    this._from.addEventListener('blur', () => this.onBlur());
    this._to.addEventListener('keyup', () => this.onInput());
    this._to.addEventListener('focus', () => this.onFocus());
    this._to.addEventListener('blur', () => this.onBlur());
  }
  onInput() {
	  const fromValue = this.filter(this._from.value);
	  const toValue = this.filter(this._to.value);
	  if (fromValue > this.slider.max) {
		  this._from.value = this.slider.max;
	  }
	  if (fromValue < this.slider.min) {
		  this._from.value = this.slider.min;
	  }
	  if (toValue > this.slider.max) {
		  this._to.value = this.slider.max;
	  }
	  if (toValue < this.slider.min) {
		  this._to.value = this.slider.min;
	  }
  }
  onFocus() {
    this._from.value = this.filter(this._from.value);
    this._to.value = this.filter(this._to.value);
  }
  onBlur() {
	  this._from.value = `${Number(this._from.value).toLocaleString()} ${this._from.dataset.units}`;
	  this._to.value = `${Number(this._to.value).toLocaleString()} ${this._to.dataset.units}`;
  }
  onChange() {
    this._values = [this._from.value, this._to.value];
    this.slider.values = this._values;
  }
  set values(values) {
    this._from.value = `${values[0].toLocaleString()} ${this._from.dataset.units}`;
    this._to.value = `${values[1].toLocaleString()} ${this._to.dataset.units}`;
    this._values = values;
  }
  get values() {
    return this._values;
  }
  filter(value) {
    const string = value.toString().replace(/\D/mg,'');
    return parseInt(string);
  }
}
